irection:row;justify-content:space-between}}@media all and (max-width: 900px){.AppHeader{flex-direction:column;justify-content:center}.AppHeader h1{padding-top:3rem;padding-left:0;margin-bottom:0}}@media all and (max-width: 600px){.AppHeader{flex-direction:column;justify-content:center;margin-bottom:0}}
